@import "Variables.scss";
@import "MediaQueries.scss";

.tile {
  padding: 4rem;
  background: $primary;

    &__container {
      display: flex;
      flex-flow: column;
      align-items: center;
      max-width: 1120px;
      width: 90%;
      margin: 0 auto;
    }
    &__wrapper {
      position: relative;
      margin: 50px 0 45px;
    }
    &__items {
      margin-bottom: 24px;
    }
    &__item {
      display: flex;
      flex: 1;
      margin: 0 1rem;
      border-radius: 10px;
    }
    &__item__link {
      display: flex;
      flex-flow: column;
      width: 100%;
      box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
      -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
      filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
      border-radius: 10px;
      overflow: hidden;
      text-decoration: none;
    }
    &__item__pic-wrap {
      position: relative;
      width: 100%;
      padding-top: 67%;
      overflow: hidden;
    }
    &__item__pic-wrap::after {
      content: attr(data-category);
      position: absolute;
      bottom: 0;
      margin-left: 10px;
      padding: 6px 8px;
      max-width: calc((100%) - 60px);
      font-size: 12px;
      font-weight: 700;
      color: $primary;
      background-color: #1f98f4;
      box-sizing: border-box;
    }
    &__item__img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      max-width: 100%;
      height: 100%;
      max-height: 100%;
      object-fit: cover;
      transition: all 0.2s linear;
    }
    &__item__img:hover {
      transform: scale(1.1);
    }
    &__item__info {
      padding: 20px 30px 30px;
    }
    &__item__text {
      color: #252e48;
      font-size: 18px;
      line-height: 24px;
    }
}
.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

h1 {
  text-align: center;
}

@include tile-small-devices-v1 {
  .content__blog__container {
    width: 90%;
  }
}

@include tile-small-devices-v2 {
  .tile__items {
    display: flex;
  }
}

@include tile-small-devices-v3 {
  .tile__item {
    margin-bottom: 2rem;
  }
}
