@import "Variables.scss";

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
  
    &--primary {
      background-color: $primary;
      color: $secondary;
      border: 1px solid $primary;
    }
    &--outline {
      background-color: transparent;
      color: $primary;
      padding: 8px 20px;
      border: 1px solid $primary;
      transition: all 0.3s ease-out;
    }
    &--medium {
      padding: 8px 20px;
      font-size: 18px;
    }
    &--large {
      padding: 12px 26px;
      font-size: 20px;
    }
    
    &--large:hover, &--medium:hover {
      background: $primary;
      color: $secondary;
      transition: all 0.3s ease-out;
    }
}